import getLanguage from "./API/api";
import applyDiff from "./applyDiff";
import registry from "./registry";
import searchedResultView from "./view/searched";
import AppView from "./view/app";

// ë ˆì§€ìŠ¤íŠ¸ë¦¬ì— í•¨ìˆ˜ëª…-í•¨ìˆ˜(VDOM ìƒì„± ìœ„í•œ) ë“±ë¡
registry.add("SearchedResult", searchedResultView);
registry.add("app", AppView);

// *ìƒíƒœ -> ì˜¤ì§ ìƒíƒœì—ë§Œ ìˆœìˆ˜í•˜ê²Œ ì˜ì¡´í•˜ì—¬ ë Œë”ë§ ë˜ëŠ” í™”ë©´ êµ¬í˜„
const state = {
  target: "",
  searchList: [],
  clickList: [],
  curFocusedItem: "",
};

// *ì´ë²¤íŠ¸
// (1) ê²€ìƒ‰ì–´ ì…ë ¥ ì‹œ ê²€ìƒ‰ ê²°ê³¼ ë“±ì¥
// (2) ì„ íƒ(enter / click) ì‹œ ê²€ìƒ‰ ëª©ë¡ì— ì €ì¥
const events = {
  setSearchedItems: ({ target, searchList, curFocusedItem }) => {
    state.target = target;
    state.searchList = searchList;
    state.curFocusedItem = curFocusedItem;
    render();
  },
  addClickList: (target) => {
    state.clickList.push(target);
    if (state.clickList.length > 5) state.clickList = state.clickList(1);
    render();
  },
  upperItem: () => {
    state.curFocusedItem =
      state.searchList[
        (state.searchList.indexOf(state.curFocusedItem) -
          1 +
          state.searchList.length) %
          state.searchList.length
      ];
    render();
  },
  lowerItem: () => {
    state.curFocusedItem =
      state.searchList[
        (state.searchList.indexOf(state.curFocusedItem) + 1) %
          state.searchList.length
      ];
    render();
  },
};

// ë Œë”ë§ í•¨ìˆ˜
const render = () => {
  window.requestAnimationFrame(() => {
    const main = document.querySelector("#root");
    const newMain = registry.renderRoot(main, state, events);
    applyDiff(document.body, main, newMain);
  });
};

// ìœ„-ì•„ë˜ ì´ë™ : ì´ ë¶€ë¶„ì— ëŒ€í•´ì„œ ì „ì—­ìœ¼ë¡œ ëº´ëŠ”ê²Œ ë§ëŠ”ì§€?
window.addEventListener("keydown", (e) => {
  if (state.searchList !== 0) {
    if (e.key === "ArrowDown") {
      events.lowerItem();
    }
    if (e.key === "ArrowUp") {
      events.upperItem();
    }
    if (e.key === "Enter") {
    }
  }
});

render();
