// nodeê°€ ë³€í™”ë˜ì—ˆëŠ”ì§€ ì•ˆë˜ì—ˆëŠ”ì§€ì— ëŒ€í•œ ê²ƒì„ ê°ì§€í•˜ëŠ” ì½”ë“œ
const isNodeChanged = (node1, node2) => {
  // nodeì˜ attributeë“¤ì„ ë‚˜ì—´
  const n1Attributes = node1.attributes;
  const n2Attributes = node2.attributes;

  //*  ì–´íŠ¸ë¦¬ë·°íŠ¸ ë…¸ë“œ ë¹„êµ */
  // ì†ì„± ìˆ˜ê°€ ë‹¤ë¥´ë©´ ì¼ë‹¨ ì°¸ì´ë¼ê³  (ë‹¤ë¥´ë‹¤!)
  if (n1Attributes.length !== n2Attributes.length) {
    return true;
  }

  // ë‹¤ë¥¸ ì†ì„±ì´ ìˆë‹¤ë©´..
  const differentAttribute = Array.from(n1Attributes).find((attribute) => {
    const { name } = attribute;
    const attribute1 = node1.getAttribute(name);
    const attribute2 = node2.getAttribute(name);

    return attribute1 !== attribute2;
  });

  // ë‹¤ë¥¸ ì†ì„±ì´ ìˆë‹¤ë©´ ì°¸(ë‹¤ë¥´ë‹¤!)
  if (differentAttribute) {
    return true;
  }

  //* ë§Œì•½ ìì‹ìš”ì†Œê°€ ì—†ë‹¤ë©´ í…ìŠ¤íŠ¸ ë…¸ë“œ ë¹„êµ */
  // í…ìŠ¤íŠ¸ê°€ ë‹¤ë¥´ë‹¤ë©´ ì°¸(ë‹¤ë¥´ë‹¤!)
  if (
    node1.children.length === 0 &&
    node2.children.length === 0 &&
    node1.textContent !== node2.textContent
  ) {
    return true;
  }

  // ì†ì„± ìˆ˜ ê°™ê³ , ì†ì„± ëª¨ë‘ ê°™ê³  + ìì‹ìš”ì†Œ ìˆëŠ”ë° í…ìŠ¤íŠ¸ ë…¸ë“œê¹Œì§€ ê°™ë‹¤ë©´ ê±°ì§“(ê°™ë‹¤!)
  return false;
};

// íŠ¸ë¦¬ êµ¬ì¡°ì˜ ë³€í™” ë¹„êµ -> ë³€í™”ë¥¼ ë°”íƒ•ìœ¼ë¡œ DOM íŠ¸ë¦¬ì—ì„œ ë…¸ë“œë“¤ì„ ë³€ê²½í•˜ê±°ë‚˜ ì‚­ì œí•œë‹¤.
const applyDiff = (parentNode, realNode, virtualNode) => {
  // ì‹¤ì œë…¸ë“œO , ê°€ìƒë…¸ë“œX -> ì‹¤ì œë…¸ë“œ ì œê±°
  if (realNode && !virtualNode) {
    realNode.remove();
    return;
  }

  // ì‹¤ì œë…¸ë“œX, ê°€ìƒë…¸ë“œO -> documentì— ê°€ìƒë…¸ë“œ ë°˜ì˜
  if (!realNode && virtualNode) {
    parentNode.appendChild(virtualNode);
    return;
  }

  // ì‹¤ì œë…¸ë“œì—ì„œ ê°€ìƒë…¸ë“œë¡œì˜ ë³€í™”ê°€ ìˆì—ˆë‹¤ë©´ -> ì‹¤ì œ ë…¸ë“œë¥¼ ê°€ìƒë…¸ë“œë¡œ ë³€ê²½
  if (isNodeChanged(virtualNode, realNode)) {
    realNode.replaceWith(virtualNode);
    return;
  }

  // ëª¨ë“  í•˜ìœ„ ë…¸ë“œë“¤ì— ëŒ€í•´ì„œ ë¹„êµë¥¼ ìˆ˜í–‰í•˜ì—¬
  const realChildren = Array.from(realNode.children);
  const virtualChildren = Array.from(virtualNode.children);

  // ìì‹ë“¤ì— ëŒ€í•´ì„œ applyDiffë¥¼ ì¬ê·€ì ìœ¼ë¡œ ìˆ˜í–‰
  const max = Math.max(realChildren.length, virtualChildren.length);
  for (let i = 0; i < max; i++) {
    applyDiff(realNode, realChildren[i], virtualChildren[i]);
  }
};

export default applyDiff;
