import { getLanguage } from "../API/api";

let template;

const getTemplate = () => {
  if (!template) {
    template = document.querySelector("#Search-App");
  }

  // [!] template tagì˜ attributeì— ëŒ€í•´ í•œë²ˆ ì•Œì•„ë³´ë©´ ì¢‹ì„ ê²ƒ ê°™ìŒ
  return template.content.firstElementChild.cloneNode(true);
};

const addEvents = (targetElement, events) => {
  // ê²€ìƒ‰
  targetElement
    .querySelector(".SearchInput__input")
    .addEventListener("keyup", async (e) => {
      if (e.target.value !== "") {
        const result = await getLanguage(e.target.value);
        events.setSearchedItems({
          target: e.target.value,
          searchList: result,
          curFocusedItem: result.length > 0 ? result[0] : "",
        });
      } else {
        events.setSearchedItems({
          target: "",
          searchList: [],
          curFocusedItem: "",
        });
      }
    });
};

// targetElementì— data-component ì†ì„±ì´ ë¶™ì–´ìˆëŠ” elementê°€ ë“¤ì–´ê°„ë‹¤.
// ì—¬ê¸°ì„œëŠ” Search-App value ê°’ì´ ë“¤ì–´ ìˆëŠ” elementê°€ ì „ë‹¬ ë˜ê² ì§€.
export default (targetElement, state, events) => {
  const newApp = targetElement.cloneNode(true);

  // ì¼ë‹¨ VDOM ë‚´ë¶€ í•œë²ˆ ë¹„ì›Œì£¼ê³ 
  newApp.innerHTML = "";

  // template ì—†ìœ¼ë©´ ë§Œë“¤ê³ , ìˆìœ¼ë©´ ê°€ì ¸ë‹¤ ì“´ë‹¤.
  newApp.appendChild(getTemplate());

  // VDOMì— event ë“±ë¡
  addEvents(newApp, events);

  return newApp;
};
